.tab-list {
    width: 5%;
    height: 100%;   
    position: fixed;
    margin: 0;
    padding: 0;
    background-color: #555;
}

.tab-list-item {
    display: block;
    list-style: none;
    color: #ddd;
    padding: 2rem 0rem;
}

.tab-list-item:hover {
    background-color: #aaa;
}

.tab-list-active {
    background-color: #999;
}

.tab-content {
    margin-left: 5%;
}

  
  